import "./App.css";
import { Button } from "@nextui-org/react";
import WordFlicker from "./WordFlicker";
import { Menu } from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/20/solid";

const MainPage = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };

  return (
    <>
      <header>
        <div className="comb-over"></div>
        <span className="logo">
          <a href="#"> Koh Aesthetics </a>
          <a href="#" className="About">
            About
          </a>
          <div className="fixed top-16 w-56 text-right flex-grow: 2">
            <Menu as="div" className="relative inline-block text-left">
              <div>
                <Menu.Button className="inline-flex w-full justify-center rounded-md bg-black/20 px-4 py-2 text-sm font-medium text-white hover:bg-black/30 focus:outline-none focus-visible:ring-2 focus-visible:ring-white/75">
                  Services
                  <ChevronDownIcon
                    className="-mr-1 ml-2 h-5 w-5 text-violet-200 hover:text-violet-100"
                    aria-hidden="true"
                  />
                </Menu.Button>
              </div>
              <Menu.Items>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      Injectables
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      PDO Threads
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      IV Infusion
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      Microneedling
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      Consultation
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      Weight Loss Program
                    </a>
                  )}
                </Menu.Item>
                <Menu.Item>
                  {({ active }) => (
                    <a
                      className={`${active && "bg-blue-500"}`}
                      href="/account-settings"
                    >
                      Hair Restoration
                    </a>
                  )}
                </Menu.Item>
              </Menu.Items>
            </Menu>
          </div>
        </span>
      </header>
      <main>
        <div className="background" src="">
          <div
            className="app-button"
            style={{
              backgroundColor: "#C49DBC",
              color: "#FFFFFF",
              padding: "20px",
              borderRadius: "10px",
              border: "2px solid #050511",
              textAlign: "center",
              right: "10",
              width: "500px",
              height: "500px",
            }}
          >
            <h1
              style={{
                fontSize: "20px",
                marginBottom: "10px",
                color: "#FFFFFF",
              }}
            >
              Welcome to Koh Aesthetics!
            </h1>
            <p
              style={{
                fontSize: "15px",
                marginBottom: "20px",
                color: "#FFFFFF",
              }}
            >
              Welcome to Dr. Koh Aesthetics! We're here to enhance your natural
              beauty with personalized care. Dr. Koh is dedicated to providing
              individualized aesthetic solutions that highlight your unique
              features. Interested in a personalized consultation? Book an
              appointment today!
            </p>
            <Button
              onClick={redirectToLink}
              style={{
                backgroundColor: "#FFFDF9",
                color: "#323232",
                fontWeight: "500",
                width: "100%",
              }}
            >
              Book an Appointment
            </Button>
          </div>
        </div>
        <WordFlicker />
      </main>
    </>
  );
};

export default MainPage;
